* {
	margin: 0;
	padding: 0;
}

*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

:root {
	font-size: 10px;
}

main {
	display: block;
}

img {
	-ms-interpolation-mode: bicubic;
	border: 0 none;
	text-decoration: none;
	vertical-align: top;
	background: transparent;
}

mark {
	background-color: transparent;
}

a:link {
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

a:focus {
	text-decoration: underline;
}

.test {
	-webkit-transition: all 0.5s cubic-bezier(1, 0, 0, 1);
	transition: all 0.5s cubic-bezier(1, 0, 0, 1);
	background-image: linear-gradinet(to, bottom, rgba(0,0,0, 1) 0%, rgba(0,0,0, 0.738) 19%, rgba(0,0,0, 0.541) 34%, rgba(0,0,0, 0.382) 47%, rgba(0,0,0, 0.278) 56.5%, rgba(0,0,0, 0.194) 65%, rgba(0,0,0, 0.126) 73%, rgba(0,0,0, 0.075) 80.2%, rgba(0,0,0, 0.042) 86.1%, rgba(0,0,0, 0.021) 91%, rgba(0,0,0, 0.008) 95.2%, rgba(0,0,0, 0.002) 98.2%, rgba(0,0,0, 0) 100%);
}

